.overlayMenu {
    position: absolute;
    left: 4.5em;
    top: 1em;
    right: 4.5em;
    background-color: var(--menu-background);
    color: var(--menu-font-color);
    border: 1px solid var(--menu-border-color);
    z-index: 2;
    padding: 0 4px;
    padding-bottom: 4px;
}

.overlayMenu .keyboard-menu {
    position: relative;
    border: 0;
}

.overlayMenu h2 {
    font-size: 10px;
    color: var(--menu-font-color);
    margin: 0;
    padding: 2px 0;
    text-align: center;
    border-bottom: 1px solid #A48465;
}